<template>
  <div class="login">
    <h1>登陆</h1>
    <el-input
      v-show="isShow"
      class="text"
      v-model="email"
      placeholder="邮箱"
    ></el-input>
    <el-input
      v-show="isShow"
      class="text"
      v-model="name"
      placeholder="昵称"
    ></el-input>
    <el-input class="text" v-model="username" placeholder="用户名"></el-input>
    <el-input
      class="text"
      placeholder="请输入密码"
      v-model="password"
      show-password
    ></el-input>
    <el-input
      v-show="isShow"
      class="text"
      v-model="Repassword"
      placeholder="重复密码"
    ></el-input>
    <el-row>
      <el-button class="btn" type="primary" @click="login">登陆</el-button>
      <el-button class="btn" type="primary" @click="isShowRegister"
        >显示注册</el-button
      >
      <el-button class="btn" type="primary" @click="register">注册</el-button>
    </el-row>
  </div>
</template>

<script>
export default {
  data() {
    return {
      username: "",
      password: "",
      Repassword: "",
      email: "",
      name: "",
      isShow: false,
      loginShow: true,
      user: {
        username: this.username,
        password: this.password,
        email: this.email,
        name: this.name,
      },
    };
  },

  methods: {
    isShowRegister() {
      this.isShow = !this.isShow;
    },
    register() {
      if (
        this.isShow &&
        this.name != "" &&
        this.email != "" &&
        this.username != "" &&
        this.password != ""
      ) {
        localStorage.setItem("@username", this.username);
        localStorage.setItem("@password", this.password);
        let data = {
          username: this.username,
          password: this.password,
          email: this.email,
          name: this.name,
        };
        localStorage.setItem("@user", JSON.stringify(data));
        alert("注册成功");
        this.$router.go(0);
      } else {
        alert("请点击显示注册,并且填写信息");
      }
    },
    login() {
      if (localStorage.getItem("@username") === this.username) {
        if (localStorage.getItem("@password") === this.password) {
          var item = JSON.parse(localStorage.getItem("@user"));
          this.$bus.$emit("closeLogin");
          alert("你好用户" + item.name + ",登陆成功,点击确认3s后返回首页");
          this.timer = setInterval(() => {
            this.$route.params.username = this.username;
            this.$route.params.password = this.password;
            this.$router.push({
              name: "TheIndex",
            });
          }, 3000);
        } else {
          alert("密码错误");
        }
      } else {
        alert("账号不存在");
      }
    },
  },

  beforeDestroy() {
    clearInterval(this.timer);
  },
};
</script>

<style scoped>
.btn {
  margin: 20px 50px;
  background-color: rgb(144, 129, 241);
  border: violet;
}
.text {
  width: 40%;
  margin: 20px;
}

h1 {
  margin: 20px 0px;
}
.login {
  text-align: center;
  margin: 5px auto;
  color: rgb(144, 129, 241);
}
</style>